html.has-scroll-smooth {
  overflow: hidden;
}

html.has-scroll-dragging {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.has-scroll-smooth body {
  overflow: hidden;
}

.c-scrollbar {
  position: absolute;
  top: 0;
  right: 0;
  // display: none;
  width: 11px;
  height: 100vh;
  transform-origin: center right;
  transition: transform 0.3s, opacity 0.3s;
  opacity: 0;
}
.c-scrollbar:hover {
  transform: scaleX(1.45);
}
.c-scrollbar:hover,
.has-scroll-scrolling .c-scrollbar,
.has-scroll-dragging .c-scrollbar {
  opacity: 1;
}

.c-scrollbar_thumb {
  position: absolute;
  top: 0;
  right: 0;
  margin: 2px;
  opacity: 0.5;
  width: 7px;
  background-color: #07c160;
  cursor: -webkit-grab;
  cursor: grab;
}
.has-scroll-dragging .c-scrollbar_thumb {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

#js-scroll {
  position: relative;
  z-index: 1;
}

.scrollbar-track {
  display: none !important;
}

.app_notready {
  * {
    animation-play-state: paused !important;
  }
}

.app_ready {
  * {
    animation-play-state: initial !important;
  }
}

.app {
  position: fixed;
  z-index: 2;
  pointer-events: none;
  top: 0;
  left: 0;
  min-width: 100vw;
  height: 100vh;

  &-wrapper {
    height: 100%;
  }

  &-cursor {
    position: fixed;
    z-index: 1030;
    top: 0;
    left: 0;
    margin-left: -10px;
    margin-top: -10px;
    width: 20px;
    height: 20px;
    // background-color: #fff;
    border: 2px solid;
    border-color: #000;
    border-radius: 100%;
    transform-origin: center center;
    pointer-events: none;
    transition: 0.1s ease transform, 0.2s ease border-color;
    // mix-blend-mode: exclusion;

    &-inner {
      opacity: 0;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -7px;
      margin-left: -7px;
      width: 14px;
      height: 14px;
      background-color: #07c160;
      transform: rotate(-45deg);
      transition: 0.2s ease all;

      &::before,
      &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: inherit;
        border-radius: 100%;
      }

      &::before {
        bottom: 7px;
        left: 0;
      }
      &::after {
        top: 0;
        left: 7px;
      }
    }

    &_stuck {
      border-color: transparent;

      .app-cursor {
        &-inner {
          opacity: 0.8;
        }
      }
    }
  }
}
