.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-20%, -50%, 0) scale(1.5);
  width: 100px;
  height: 100px;
  opacity: 0.8;
  transition: 2.5s ease all;

  &.loading_done {
    opacity: 0;
    visibility: hidden;
    transform: translate3d(-20%, -800%, 0) scale(2.5);
  }
}
.load-btn {
  &-inner {
    &-heart {
      position: absolute;
      top: 0;

      svg {
        animation: triangulate 2.4s ease infinite both;
      }
      &:nth-of-type(1) {
        svg {
          animation-delay: -0.8s;
        }
      }

      &:nth-of-type(2) {
        svg {
          animation-delay: 0;
        }
      }

      &:nth-of-type(3) {
        svg {
          animation-delay: -1.6s;
        }
      }
    }
  }
}

@keyframes triangulate {
  0% {
    transform: translate3d(0, 0, 0);
  }
  33% {
    transform: translate3d(250%, 0, 0);
  }
  66% {
    transform: translate3d(125%, calc(86.60259% * 2 * 1.25), 0);
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}
